import {Component, OnInit, HostBinding, Input} from '@angular/core';
import {Router} from '@angular/router';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {TranslateService} from '@ngx-translate/core';

import {MenuItems} from '../../../Core/menu/menu-items/menu-items';
import {AuthService} from '../../../Services/auth.service';
import {CookieService} from 'ngx-cookie-service';

@Component({
    selector: 'embryo-Menu',
    templateUrl: './Menu.component.html',
    styleUrls: ['./Menu.component.scss'],
    animations: [
        trigger('indicatorRotate', [
            state('collapsed', style({transform: 'rotate(0deg)'})),
            state('expanded', style({transform: 'rotate(180deg)'})),
            transition('expanded <=> collapsed',
                animate('225ms cubic-bezier(0.4,0.0,0.2,1)')
            ),
        ])
    ]
})
export class MenuComponent implements OnInit {

    expanded: boolean;

    constructor(public cookieService: CookieService, public menuItems: MenuItems, public router: Router, public translate: TranslateService, public authService: AuthService) {
    }

    ngOnInit() {
        // this.cookieService.get('is_staff');

        if (this.cookieService.get('is_staff') === 'true' && this.cookieService.get('access_token') !== null) {
            this.authService.showAdminPanel = 'true';
            this.authService.showLogin = 'false';
        } else if (this.cookieService.get('is_staff') === 'false' && this.cookieService.get('access_token') !== null) {
            this.authService.showAdminPanel = 'false';
            this.authService.showLogin = 'false';

        } else {
            this.authService.showLogin = 'true';

        }
    }

    public onItemSelected(item: any) {
        if (item.children && item.children.length) {
            this.expanded = !this.expanded;
        }
    }


    public redirectTo(subchildState) {
        this.router.navigate([subchildState.state], {queryParams: {category: subchildState.queryState}});
    }

}
