import {Component, OnInit} from '@angular/core';
import {CookieService} from 'ngx-cookie-service';
import {Router} from '@angular/router';
import {AuthService} from '../../Services/auth.service';
import {ToastaService, ToastOptions} from 'ngx-toasta';

@Component({
    selector: 'embryo-SignIn',
    templateUrl: './CommonSignIn.component.html',
    styleUrls: ['./CommonSignIn.component.scss']
})
export class CommonSignInComponent implements OnInit {
    toastOption: ToastOptions = {
        title: 'Login',
        msg: 'Welcome To Events Application',
        showClose: true,
        timeout: 3000,
        theme: 'material'
    };
    errorMessage;
    hide = true;

    constructor(public authService: AuthService, private cookieService: CookieService, public router: Router, private toastyService: ToastaService) {
    }

    ngOnInit() {
    }

    onSubmit() {
        this.errorMessage = null;
        this.authService.postLogin().subscribe((res: { code, data, refresh_token, is_staff, id, access_token, email }) => {
            console.log(res);
            if (res.code === 200) {
                this.cookieService.set('access_token', res.data.access_token, null, '/');
                this.cookieService.set('refresh_token', res.refresh_token, null, '/');
                this.cookieService.set('token_type', res.data.token_type, null, '/');
                this.authService.showLogin = 'false';
                console.log(res.data.user.roles[0].name);
                if (res.data.user.roles[0].name === 'app-admin') {
                    this.authService.showAdminPanel = 'true';
                    this.cookieService.set('is_staff', 'true', null, '/');

                    this.router.navigate(['/admin-panel']);
                } else {
                    this.cookieService.set('is_staff', 'false', null, '/');
                    this.authService.showAdminPanel = 'false';

                    this.router.navigate(['/home']);
                }
                // this.toastyService.success(this.toastOption);
            }
        }, error => {
            if (error.error.status_code === 400) {
                if (error.error.email) {
                    if (error.error.email[0] === 'Not found') {
                        this.errorMessage = 'User Not Found';
                    }
                }
                if (error.error.password) {
                    if (error.error.password[0] === 'Ensure this field has at least 8 characters.') {
                        this.errorMessage = 'Password Must Be 8 Characters';
                    } else if (error.error.password[0] === 'Wrong password') {
                        this.errorMessage = 'Password Is Not Correct';
                    }
                }
            }
        });
    }
}
