import {Injectable} from '@angular/core';
import {DataService} from './data.service';
import {ITableData} from '../Models/table-data';

@Injectable({
    providedIn: 'root'
})
export class TaskTypesService {

    AddTaskType = {} as ITableData;

    constructor(public dataService: DataService) {
    }


    getTaskTypes() {
        return this.dataService.get('task_types');
    }


    AddTaskTypes() {
        return this.dataService.post('admin/task_types', this.AddTaskType);
    }

    deleteTaskTypes(id: number) {
        return this.dataService.delete('admin/task_types' + '/', id);

    }

    editTaskTypes(id: number) {
        return this.dataService.update('admin/task_types' + '/' + id, this.AddTaskType);

    }
}
