import {Injectable} from '@angular/core';
import {CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router} from '@angular/router';
import {Observable} from 'rxjs';
import {CookieService} from 'ngx-cookie-service';
import {ToastaService} from 'ngx-toasta';

@Injectable({
    providedIn: 'root'
})
export class RoleGuard implements CanActivate {
    constructor(private cookieService: CookieService, private router: Router, private toastyService: ToastaService) {
    }

    canActivate(
        route: ActivatedRouteSnapshot,
        state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
        const isStuff = this.cookieService.get('is_staff');
        if (isStuff === 'false') {
            this.router.navigate(['/home']);
            this.toastyService.success({
                title: 'Authorization',
                msg: 'Sorry You Dont Have The Access To Be In The Admin Panel',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            return false;
        } else {
            return true;
        }
    }


}
