import {Component, OnInit} from '@angular/core';
import {CookieService} from 'ngx-cookie-service';
import {AuthService} from '../../Services/auth.service';
import {Router} from '@angular/router';

@Component({
    selector: 'embryo-HeaderUserProfileDropdown',
    templateUrl: './HeaderUserProfileDropdown.component.html',
    styleUrls: ['./HeaderUserProfileDropdown.component.scss']
})
export class HeaderUserProfileDropdownComponent implements OnInit {

    constructor(private cookieService: CookieService, private router: Router, public authService: AuthService) {
    }

    ngOnInit() {
    }

    logOut() {
        this.router.navigate(['/session/signin']);
        this.cookieService.delete('access_token', '/');
        this.cookieService.delete('refresh_token', '/');
        this.cookieService.delete('is_staff', '/');
        this.cookieService.delete('token_type', '/');
        this.authService.showAdminPanel = 'false';
        this.authService.showLogin = 'true';
        // console.log("entered logout");
        return false;
    }
}
