import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {Routes} from '@angular/router';
import {MainAdminPanelComponent} from './Main/Main.component';
import {AuthGuard} from '../Guards/auth.guard';
import {RoleGuard} from '../Guards/role.guard';

export const AdminPanelRoutes: Routes = [
    {
        path: 'admin-panel',
        redirectTo: 'admin-panel/reports',
        pathMatch: 'full',
    },
    {
        path: 'admin-panel',
        component: MainAdminPanelComponent,
        children: [
            {
                path: 'reports', loadChildren: () =>
                    import('./Reports/Reports.module').then(m => m.ReportsModule), canActivate: [AuthGuard, RoleGuard],
            },
            {
                path: 'invoices', loadChildren: () =>
                    import('./Invoices/Invoices.module').then(m => m.InvoicesModule), canActivate: [AuthGuard, RoleGuard],
            },
            {
                path: '', loadChildren: () =>
                    import('./Products/Products.module').then(m => m.ProductsModule), canActivate: [AuthGuard, RoleGuard],
            },
            {
                path: 'account', loadChildren: () =>
                    import('./AdminAccount/AdminAccount.module').then(m => m.AdminAccountModule), canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'supervisors', loadChildren: () =>
                    import('./supervisors/supervisors.module').then(m => m.SupervisorsModule), canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'roles', loadChildren: () =>
                    import('./roles/roles.module').then(m => m.RolesModule), canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'permeations', loadChildren: () =>
                    import('./permeations/permeations.module').then(m => m.PermeationsModule), canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'farms', loadChildren: () =>
                    import('./farms/farms.module').then(m => m.FarmsModule), canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'posts', loadChildren: () =>
                    import('./posts/posts.module').then(m => m.PostsModule), canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'task-type', loadChildren: () =>
                    import('./task-type/task-types.module').then(m => m.TaskTypesModule), canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'measuring-units',
                loadChildren: () =>
                    import('./measuring-units/measuring-units.module').then(m => m.MeasuringUnitsModule),
                canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'farmedType-blog',
                loadChildren: () =>
                    import('./blog-farmed-type/blog-farmed-type.module').then(m => m.BlogFarmedTypeModule),
                canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'product-info',
                loadChildren: () =>
                    import('./products-information/products-information.module').then(m => m.ProductsInformationModule),
                canActivate: [AuthGuard, RoleGuard],
            }, {
                path: 'jobs',
                loadChildren: () =>
                    import('./jop/jop.module').then(m => m.JopModule),
                canActivate: [AuthGuard, RoleGuard],
            },
        ]
    }];
