import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {DataService} from './data.service';
import * as http from 'http';
import {IAppAdmin} from '../Models/app-admin';
import {IAddUser, IUsersAdmin} from '../Models/users-admin';
import {IAddType} from '../Models/admin-posts';

@Injectable({
    providedIn: 'root'
})
export class AppAdminService {
    AddRole = {} as IAppAdmin;
    updateRole = {} as IAppAdmin;
    AddUser = {} as IAddUser;
    updateUsers = {} as IAddUser;
    AddType = {} as IAddType;
    updateType = {} as IAddType;

    constructor(private httpClient: HttpClient, private dataService: DataService) {
    }

    getRoles() {
        return this.dataService.get('admin/roles');
    }

    addRoles() {
        return this.dataService.post('admin/roles', this.AddRole);
    }

    updateRoles(id) {
        return this.dataService.update('admin/roles/' + id, this.updateRole);

    }

    getAllPermissions() {
        return this.dataService.get('admin/permissions');

    }

    deleteRole(id: number) {
        return this.dataService.delete('admin/roles/', id);

    }

    getRoleID(id: number) {
        return this.dataService.get('admin/roles/' + id);

    }

    getPermission() {
        return this.dataService.get('admin/permissions');
    }

    addPermission() {
        return this.dataService.post('admin/permissions', this.AddRole);
    }

    updatePermission(id) {
        return this.dataService.update('admin/permissions/' + id, this.updateRole);

    }

    // getAllPermissions() {
    //     return this.dataService.get('admin/permissions');
    //
    // }

    deletePermission(id: number) {
        return this.dataService.delete('admin/permissions/', id);

    }


    getPermissionID(id: number) {
        return this.dataService.get('admin/permissions/' + id);

    }

    ///////////////////////////////////////////////////////////////////////////////////
    getUsers() {
        return this.dataService.get('admin/generic_users');
    }

    addUser() {
        return this.dataService.post('admin/users', this.AddUser);
    }

    updateUser(id) {
        return this.dataService.update('admin/users/' + id, this.updateUsers);

    }

    // getAllPermissions() {
    //     return this.dataService.get('admin/permissions');
    //
    // }

    deleteUser(id: number) {
        return this.dataService.delete('admin/users/', id);

    }

    getUserID(id: number) {
        return this.dataService.get('admin/generic_users/' + id);

    }

    getUserToggle_activate(id: number) {
        return this.dataService.get('admin/users/toggle_activate/' + id);

    }

    //////////////////////////////////////////////////////////////////
    getPosts() {
        return this.dataService.get('admin/posts');
    }

    getPostID(id: number) {
        return this.dataService.get('posts/' + id);

    }

    addPost() {
        return this.dataService.post('admin/users', this.AddUser);
    }

    updatePost(id) {
        return this.dataService.update('admin/users/' + id, this.updateUsers);

    }

    deletePost(id: number) {
        return this.dataService.delete('posts/', id);

    }

    getPostToggle_activate(id: number) {
        return this.dataService.get('admin/posts/toggle_activate/' + id);

    }

    ///////////////////////////////////////////////////////////
    getPostType() {
        return this.dataService.get('post_types');
    }

    getPostTypeById(id: number) {
        return this.dataService.get('admin/post_types/' + id);
    }

    addType() {
        return this.dataService.post('admin/post_types', this.AddType);

    }
    updatePostType(id) {
        return this.dataService.update('admin/post_types/' + id, this.updateType);

    }
}
