import {Component, OnInit} from '@angular/core';
import {IAppAdmin} from '../../Models/app-admin';
import {IAddFarmedType, IAddSoilTypes, IFarmedType} from '../../Models/farms';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../Services/app-admin.service';
import {FarmsService} from '../../Services/farms.service';
import {ToastaService} from 'ngx-toasta';
import {AddEditIrrigationWaysComponent} from '../farms/irrigation-ways/add-edit-irrigation-ways/add-edit-irrigation-ways.component';
import {DeleteIrrigationWaysComponent} from '../farms/irrigation-ways/delete-irrigation-ways/delete-irrigation-ways.component';
import {AddEditTaskComponent} from './add-edit-task/add-edit-task.component';
import {TaskTypesService} from '../../Services/task-types.service';
import {DeleteTypeComponent} from '../farms/farmed-type/delete-type/delete-type.component';
import {DeleteTaskTypeComponent} from './delete-task-type/delete-task-type.component';
import {ITableData} from '../../Models/table-data';

@Component({
    selector: 'app-task-type',
    templateUrl: './task-type.component.html',
    styleUrls: ['./task-type.component.css']
})
export class TaskTypeComponent implements OnInit {

    cols: any;
    products: any;
    roles: IAppAdmin[];
    farms: ITableData[];
    profileFarm: IFarmedType;
    rolesPermations: any[];
    display: boolean = false;
    data: any;
    // toastOption: ToastOptions = {
    //     title: 'Events',
    //     msg: 'This Event Has Been Deleted successfully!',
    //     showClose: true,
    //     timeout: 3000,
    //     theme: 'material'
    // };
    errorMessage: string;
    profileFarmData: IAddFarmedType;

    constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private _TaskTypesService: TaskTypesService,
                private _FarmsService: FarmsService, private toastyService: ToastaService) {
    }

    //
    ngOnInit(): void {
        this.cols = [
            {field: 'id', header: 'ID'},
            {field: 'name_ar_localized', header: 'Task Types (Ar)'},
            {field: 'name_en_localized', header: 'Task Types (En)'},


        ];
        this.getTableData();
    }

    // showDialogGetFarmedTypeById(id: number) {
    //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
    //         this.profileFarm = res.data as IFarmedType;
    //         console.log(this.profileFarm);
    //     }, error => {
    //
    //     }, () => {
    //         this.display = true;
    //
    //     });
    // }

    getTableData() {
        this._TaskTypesService.getTaskTypes().subscribe((res: { data }) => {
            this.farms = res.data.all as ITableData[];
            // this.rolesPermations = this.roles.map(x => x.permissions);
            // console.log(this.rolesPermations);
        });
    }


    openAddDataDialog() {

        const dialogRef = this.dialog.open(AddEditTaskComponent, {
                data: {
                    FarmedData: this._TaskTypesService.AddTaskType, error: this.errorMessage,
                }
            })
        ;
        dialogRef.afterClosed().subscribe(result => {
            this.errorMessage = null;

            if (result) {
                this._TaskTypesService.AddTaskType = result.FarmedData;
                this.onAddData();

            } else {
                this._TaskTypesService.AddTaskType = {} as ITableData;

            }
        });
    }

    onAddData() {
        this._TaskTypesService.AddTaskTypes().subscribe(res => {
        }, error => {

            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getTableData();
            this._TaskTypesService.AddTaskType = {} as ITableData;

            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    // getDataById(id: number) {
    //     this._FarmsService.getFarmedTypeById(id).subscribe((res: { data }) => {
    //         this.profileFarmData = res.data as IAddFarmedType;
    //         console.log(this.profileFarmData);
    //     }, error => {
    //
    //     }, () => {
    //         // this.display = true;
    //     });
    //
    //
    // }

    openEditDataDialog(classData: ITableData) {


        const dialogRef = this.dialog.open(AddEditTaskComponent, {
            data: {FarmedData: classData}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                this._TaskTypesService.AddTaskType.name_ar_localized = result.FarmedData.name_ar_localized;
                this._TaskTypesService.AddTaskType.name_en_localized = result.FarmedData.name_en_localized;

                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onEditFarmedType(result.FarmedData.id);

            } else {
                this._TaskTypesService.AddTaskType = {} as ITableData;

            }
        });
    }

    onEditFarmedType(roleId) {
        this._TaskTypesService.editTaskTypes(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getTableData();
            this._TaskTypesService.AddTaskType = {} as ITableData;

            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Updated successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    openDeleteDataDialog(data) {
        const dialogRef = this.dialog.open(DeleteTaskTypeComponent, {
            data: {farmData: data}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onDeleteData(result);

            }
        });
    }

    onDeleteData(roleId) {
        this._TaskTypesService.deleteTaskTypes(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getTableData();
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }


}
